<template>
  <div>
    <b-button
      id="add-to-cart"
    :aria-controls="'sidebar-right-footer'+productId"
      :aria-expanded="show"
      @click="initialHandler(true)"
    >
      Add to Cart
    </b-button>
    <b-button
      id="buy-now"
      :aria-controls="'sidebar-right-footer-buy'+productId"
      aria-expanded=""
      @click="initialHandler(false)"
    >
      Buy Now
    </b-button>
    <b-sidebar
      :id="'sidebar-right-footer'+productId"
      aria-label="Sidebar with custom footer"
      v-model="show"
      shadow
      lazy
      width=70%>
      <!-- <pre>{{this.product.specifications}}</pre>
      <pre>{{selector}}</pre> -->
      <template v-slot:footer>
        <div class="mx-auto text-center">
          <b-button squared variant="warning" @click="doReset" class="col-6">Reset</b-button>
          <b-button @click="finalHandler" squared variant="success" class="col-6" :disabled="!allSelected">
            {{cartButtonPressed ? 'Add to Cart' : 'Check Out'}}
          </b-button>
        </div>
      </template>
      <b-overlay :show="!productLoaded" opacity="0.4" rounded="sm" style="height:500px">
        <template v-slot:overlay>
          <div class="text-center">
            <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
            <p id="cancel-label">Please wait...</p>
          </div>
        </template>
        <div class="mx-auto d-flex flex-column" v-if="productLoaded">
            <b-list-group>
              <span
                v-for="(values, key, index) in product.specifications"
                :key="index">
                <b-list-group-item v-if="Array.isArray(values)">
                  <b-form-group :label="key">
                  <b-form-select style="width:70%"
                    v-model="selector[key]" :options="values" :value-field="key">
                    <template v-slot:first>
                      <b-form-select-option :value="null" disabled>-- Please select an option --</b-form-select-option>
                    </template>
                  </b-form-select></b-form-group>
                </b-list-group-item>
              </span>
              <b-list-group-item>
                <label :for="'sb-inline' + productId">Quantity</label>
                <b-form-spinbutton class="ml-2" :id="'sb-inline' + productId" v-model="quantity" inline></b-form-spinbutton>
              </b-list-group-item>
            </b-list-group>
        </div>
      </b-overlay>
    </b-sidebar>
    <!-- <span v-for="([key, values], index) in Object.entries(product.specifications)" :key="index">
    <b-form-group :label="'Choose '+key" v-if="Array.isArray(values)">
      <b-form-checkbox-group
        v-model="selector[key]"
        :options="values"
        size="sm"
        stacked
      >
      </b-form-checkbox-group>
    </b-form-group></span> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import loader from '../mixins/loader'

export default {
  name: 'SpecSelector',
  props: [
    'productId',
    'productInit'
  ],
  data () {
    return {
      product: {},
      productLoaded: false,
      selector: {},
      show: false,
      cartButtonPressed: null,
      quantity: 1
    }
  },
  computed: {
    ...mapGetters([
      'authToken'
    ]),
    allSelected () {
      if (!this.productLoaded) {
        return false
      }
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key]) && this.selector[key] === null) {
            return false
          }
        }
      }
      return true
    }
  },
  mixins: [
    loader
  ],
  methods: {
    ...mapActions([
      'addToCart'
    ]),
    loadProduct () {
      if (this.productLoaded) {
        return
      }
      const id = this.productId
      this.loader('/product', { productID: id }, `specs of product-${id}`)
        .then(response => {
          this.product = response.data
          this.product.specifications = this.product.extra
          delete this.product.extra
          this.productLoaded = true
          this.loadSelector()
        })
    },
    loadSelector () {
      this.selector = JSON.parse(JSON.stringify(this.product.specifications)) // deep(disconnected) copy
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key])) {
            this.selector[key] = null
          }
        }
      }
      console.log('done')
    },
    makeOrder () {
      const curPrice = this.product.price.sellingPrice
      return {
        products: [{
          id: this.product.productID,
          name: this.product.name,
          specifications: this.selector,
          quantity: this.quantity,
          image: this.product.images[0],
          curPrice,
          discount: this.product.price.discount,
          vat: this.product.price.vat
        }],
        subTotal: curPrice * (1 + this.product.price.vat / 100) * this.quantity,
        deliveryCharge: 60
      }
    },
    initialHandler (cartButtonPressed) {
      if (!this.authToken) {
        this.$bvModal.show('login-modal')
      } else {
        this.loadProduct()
        this.show = true
        this.cartButtonPressed = cartButtonPressed
      }
    },
    finalHandler () {
      if (!this.allSelected) {
        return
      }
      if (this.cartButtonPressed) {
        this.addToCart({
          productID: this.product.productID,
          specifications: this.selector,
          quantity: this.quantity
        }).then().catch(error => {
          console.log(error.response)
        })
      } else {
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.makeOrder()
          }
        })
      }
      this.show = false
    },
    doReset () {
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key])) {
            this.selector[key] = null
          }
        }
      }
    }
  },
  created () {
    if (this.productInit) {
      this.product = this.productInit
      this.productLoaded = true
    }
  }
}
</script>

<style scoped>
#add-to-cart {
  width: 50%;
  background-color: orange;
  color: black;
  font-size: 90%;
  white-space: nowrap;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}
#buy-now {
  width: 50%;
  background-color: lightblue;
  color: black;
  font-size: 90%;
  white-space: nowrap;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}
</style>
