<template>
  <div class="main">
    <div class="shadow mb-2 d-flex flex-row">
      <h2 class="text-dark">Flash Sale</h2>
      <b-button class="ml-auto mr-2"  variant="success" to="/flash-sale">Show more</b-button>
    </div>
    <product-scroll-section :products="flashSaleProducts"></product-scroll-section>
    <hr />
    <div class="shadow mb-2 d-flex flex-row">
      <h2 class="text-dark">Top Rated</h2>
      <b-button class="ml-auto mr-2" variant="success" to="/top-rated">Show more</b-button>
    </div>
    <product-scroll-section cass="mt-3" :products="topRatedProducts"></product-scroll-section>
    <hr />
    <div class="shadow mb-2 d-flex flex-row">
      <h2 class="text-dark">Most Sold</h2>
      <b-button class="ml-auto mr-2" variant="success" to="/most-sold">Show more</b-button>
    </div>
    <product-scroll-section :products="mostSoldProducts"></product-scroll-section>
    <hr />
    <div class="shadow mb-2 d-flex flex-row">
      <h2 class="text-dark">Our Shop</h2>
      <b-button class="ml-auto mr-2" variant="success" to="/our-shop">Show more</b-button>
    </div>
    <product-scroll-section :products="ourShopProducts"></product-scroll-section>
    <hr />
    <div class="shadow mb-2 d-flex flex-row">
      <h2 class="text-dark">New Arrivals</h2>
      <b-button class="ml-auto mr-2" variant="success" to="/new-arrivals">Show more</b-button>
    </div>
    <product-scroll-section :products="newArrivalsProducts"></product-scroll-section>
    <hr />
  </div>
</template>

<script>
import ProductScrollSection from '../components/ProductScrollSection.vue'
import loader from '../mixins/loader'

export default {
  data () {
    return {
      flashSaleProducts: null,
      topRatedProducts: null,
      mostSoldProducts: null,
      newArrivalsProducts: null,
      ourShopProducts: null
    }
  },
  components: {
    ProductScrollSection
  },
  mixins: [
    loader
  ],
  methods: {
    loadFlashSale () {
      const offset = 0
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/flash-sale', { offset, limit }, 'flash-sale-home')
        .then(response => {
          this.flashSaleProducts = response.data.products
        })
    },
    loadTopRated () {
      const offset = 0
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/top-rated', { offset, limit }, 'top-rated-home')
        .then(response => {
          this.topRatedProducts = response.data.products
        })
    },
    loadMostSold () {
      const offset = 0
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/most-sold', { offset, limit }, 'most-sold-home')
        .then(response => {
          this.mostSoldProducts = response.data.products
        })
    },
    loadNewArrivals () {
      const offset = 0
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/new-arrivals', { offset, limit }, 'new-arrivals-home')
        .then(response => {
          this.newArrivalsProducts = response.data.products
        })
    },
    loadOurShop () {
      const offset = 0
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/our-shop', { offset, limit }, 'our-shop-home')
        .then(response => {
          this.ourShopProducts = response.data.products
        })
    },
    loadAll () {
      this.loadFlashSale()
      this.loadTopRated()
      this.loadNewArrivals()
      this.loadMostSold()
      this.loadOurShop()
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadAll()
    }
  },
  created () {
    this.loadAll()
  }
}
</script>

<style scoped>
.main {
 background-color: rgb(221, 221, 221);
}
#custom-button {
  background-color: rgb(221, 210, 210);
  color: blue;
  font-size: 80%;
}
</style>
