<template>
  <div id="holder" :class="{ shadow }" @mouseenter="shadow = true" @mouseleave="shadow = false">
    <b-overlay :show="busy" opacity="0.7" spinner-large
                      spinner-variant="primary"  @hidden="onHidden">
      <!-- <router-link
        id="holder"
        tag="div"
        :to="{name: 'CertainProduct', params: { id: this.product.id } }"
        @click="show=true"
      > -->
        <span @click="clicking">
          <b-card :img-src="imgSrc" img-width="250px" img-height="250px" img-alt="product.name" img-top>
            <b-card-title style="font-size: 110%;">
              {{product.name}}
            </b-card-title>
            <b-card-text>
              <span id="prev-price">&#2547;{{prevPrice}}</span>
              <sup id="discount">-{{product.discount}}%</sup>
              <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
            </b-card-text>
          </b-card>
        </span>
      <!-- </router-link> -->
    </b-overlay>
    <spec-selector :product-id="product.id"></spec-selector>
  </div>
</template>

<script>
import SpecSelector from '../../components/SpecSelector'

export default {
  props: [
    'product'
  ],
  data () {
    return {
      busy: false,
      shadow: false
    }
  },
  components: {
    SpecSelector
  },
  computed: {
    imgSrc () {
      return this.product.image ? this.product.image.link : ''
    },
    prevPrice () {
      return Math.round(this.product.curPrice / (1 - this.product.discount / 100))
    }
  },
  methods: {
    onHidden () {
      this.$refs.button.focus()
    },
    clicking () {
      this.busy = true
      this.$router.push({ name: 'CertainProduct', params: { id: this.product.id } })
    }
  }
}
</script>

<style scoped>
#holder {
  cursor: pointer;
  padding: 0;
  text-align: center;
  /* here */
  transition: all 0.1s ease-in-out;
}
#holder:hover {
  transform: scale(1.02, 1.02);
}
#holder:after {
  z-index: -1;
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
}
#holder:hover::after {
  opacity: 1;
}
.card-body { /* this is needed */
  padding: 0;
}
#prev-price {
  font-size: 110%;
  color: grey;
  text-decoration: line-through;
}
#discount {
  font-size: 90%;
  color: red;
}
#cur-price {
  font-size: 130%;
  color: darkorange;
}
</style>
